<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>创建用户</span>
            <el-button type="primary" round>返回</el-button>
        </div>

        <el-form label-width="80px">
            <el-form-item label="用户名">
                <el-input placeholder="请输入内容" v-model="form.username"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input placeholder="请输入内容" v-model="form.password"></el-input>
            </el-form-item>
            <el-form-item label="手机号">
                <el-input placeholder="请输入内容" v-model="form.mobile"></el-input>
            </el-form-item>
            <el-form-item label="密保问题">
                <el-select v-model="form.question" placeholder="请选择">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="密保答案">
                <el-input placeholder="请输入内容" v-model="form.answer"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">提交</el-button>
                <el-button>重置</el-button>
            </el-form-item>
        </el-form>
    </el-card>
</template>
<script>
import {createUserApi} from '@/api/users.js'
export default {
    data() {
        return {
            form: {
                username: '',
                password: '',
                question: '',
                answer: '',
                mobile: ''
            },
            options: [{
                value: '您其中一位老师的名字',
                label: '您其中一位老师的名字'
                }, {
                value: 'one',
                label: 'one'
                }],
        }
    },
    methods: {
        onSubmit() {
            createUserApi({
                username: this.form.username,
                password: this.form.password,
                question: this.form.question,
                answer: this.form.answer,
                mobile: this.form.mobile
            })
            .then(res => {
                console.log(res);
                if(res.meta.state === 201) {
                    this.$message.success('创建成功！')
                }else {
                    this.$message.error('创建失败！')
                }
            })
        }
    }
}
</script>
<style scoped>
    .el-card .clearfix {
        display: flex;
        justify-content: space-between;
    }
</style>